.navbar {
    width: 100%;
    border-bottom: 1px solid #333;
    z-index: 2;
    position: fixed;
}
.navbar-bg {
    background : linear-gradient(45deg,rgb(52, 129, 170),orange);
    color: black;
    transition: 4s ease;
}
.navbar .container {
    display: flex;
    width: 100%;
    height: 80px;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
}

.navbar .nav-menu {
    display: flex;
}
.navbar  li {
 padding: 1rem;
 font-size: 1.2rem;
 text-transform: uppercase;
 font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
.hamburgers { 
    z-index: 5;
    cursor: pointer;
}


.active {
    position: absolute;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.8);
    transition: 0.7s;
    z-index: 5;
}

.nav-menu.active li {
    font-size: 2rem;
    color: #f8f8f8;
}

.nav-menu.active li:hover {
    color: var(--primary-color);
    transform: scale(1.2) ;
    transition: 0.3s ease;
    cursor: pointer;
}

@media screen and (max-width:740px) {
    .navbar .nav-menu {
        position: absolute;
        left: -200%;
    }

    .navbar .active {
        left: 0;
    }
}
